/* General Styling */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background: #f9f9f9;
  scroll-behavior: smooth; /* Enables smooth scrolling */
}

/* App Container */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

/* Main Content */
main {
  width: 100%;
  max-width: 1200px;
  padding-top: 80px; /* Ensures spacing below header */
}

/* Section Styling */
.section {
  padding: 2rem;
  margin: 1rem auto;
  max-width: 800px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* Footer */
footer {
  text-align: center;
  padding: 1rem;
  background: #008080;
  color: white;
  margin-top: 2rem;
  width: 100%;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  main {
    padding-top: 100px; /* Adjust spacing for smaller screens */
  }
}

@media (max-width: 480px) {
  .section {
    padding: 1.5rem;
    font-size: 0.9rem;
  }
}